.item {
  width: 250px;
  padding: 0 0.8rem 0.8rem 0.8rem;
  background-color: #fff;
  border-radius: 30px;
  text-align: center;
  flex-direction: column;
  display: flex;
}

.item img {
  margin-top: -3.5rem;
  width: 200px;
  margin-left: auto;
  margin-right: auto;
  object-fit: contain;
  border-radius: 10%;
}

.item-head_desc p {
  margin: 0;
  margin-bottom: 0.5rem;
}

.item-options-div {
  margin-top: 0;
  margin-bottom: 0.5rem;
  margin-left: -0.1rem;
  margin-right: auto;
}

.select-item-options {
  border-radius: 10px;
  padding-left: 5px;
  padding-right: 5px;
  margin-left: auto;
  margin-right: 0;
}

.test-div {
  margin-bottom: 0.6rem;
  font-size: 1rem;
  text-transform: capitalize;
}

.test-div-key {
  margin-left: 0;
  margin-right: auto;
  text-align: left;
  display: flex;
}

.item-option {
  text-align: center;
  text-transform: capitalize;
}

.item-head_desc {
  text-align: left;
  flex: 1;
  margin-top: 1rem;
  margin-bottom: 0.5rem;
  display: flex;
  flex-direction: column;
}

.head_desc-name {
  font-weight: 600;
  font-size: 1.4rem;
  text-transform: capitalize;
}

.head_desc-info {
  font-size: 0.8rem;
  color: #a0a0a0;
}

.item-calories-div {
  font-size: 0.9rem;
  margin-bottom: 0.5rem;
  margin-left: 0;
  margin-right: auto;
}

.item-add-button-div {
  margin-left: auto;
  margin-right: 0;
}